import Card from '../classes/Card';


function createCards(arrFromomdbAPI) {
  return arrFromomdbAPI.map((movieData) => {
    const {
      Title,
      Poster,
      Year,
      imdbID,
      imdbRating,
    } = movieData;
    if (Poster === 'N/A') return new Card('swiper', [Title, `https://www.imdb.com/title/${imdbID}/`, '../img/img-not-found.jpg', Year, imdbRating]);
    return new Card('swiper', [Title, `https://www.imdb.com/title/${imdbID}/`, Poster, Year, imdbRating]);
  });
}

export default createCards;
