import Block from './Block';

// cardData = filmName, filmSrc imghref, filmYear, filmRaiting
function cardHtml(filmName, filmSrc, imghref, filmYear, filmRaiting) {
  return `
  <div class="card">
    <a class="card__text" href="${filmSrc}">${filmName}</a>
    <a class="card__image" href="${filmSrc}"><img src="${imghref}"></a>
    <a class="card__text" href="https://en.wikipedia.org/wiki/${filmYear}">
      ${filmYear}
    </a>
    <a class="card__text" href="https://en.wikipedia.org/wiki/Rating">${filmRaiting}</a>
  </div>
`;
}

class Cards extends Block {
  constructor(place, cardData) {
    super(cardHtml(...cardData), place);
    [
      this.filmName,
      this.imghref,
      this.filmYear,
      this.filmRaiting,
    ] = cardData;
  }
}

export default Cards;
