import getMoviesData from '../network-workers/getMoviesData';
import getRuTranslate from '../network-workers/getRuTranslate';
import getRandomWord from '../network-workers/getRandomWord';
import isRu from '../functionsHelpers/isRu';
import createCards from '../functionsHelpers/createCards';
import swiper from '../blocks/swiper/swiper';
import searchField from '../blocks/search-field/search-field';


function onCrossButton(whereIdInput) {
  document.getElementById(whereIdInput).value = '';
}

function onKeyboardButton() {
  const field = document.getElementById('virtualKeyboardField');
  field.style.display = (field.style.display === 'none') ? 'block' : 'none';
}

async function onSearchButton(whereIdInput) {
  let searchPhrase = document.getElementById(whereIdInput).value;
  searchField.setHintAndRerender('loading...');
  const isLangRu = isRu(searchPhrase);
  try {
    if (isLangRu) searchPhrase = await getRuTranslate(searchPhrase);
  } catch (e) {
    searchField.setHintAndRerender(`
      No results for ${searchPhrase} <br>
      ${e}`, searchPhrase);
  }
  try {
    const omdbAPIResponse = await getMoviesData(searchPhrase);
    const cardsArray = createCards(omdbAPIResponse);
    swiper.removeAllSlides();
    cardsArray.forEach((card, i) => {
      swiper.addSlide(i, `<div class="swiper-slide">${card.html}</div>`);
    });
    searchField.setHintAndRerender(`Showing results for ${searchPhrase}`, searchPhrase);
  } catch (e) {
    searchField.setHintAndRerender(`
      No results for ${searchPhrase} <br>
      ${e}`, searchPhrase);
  }
}

async function onRandomWordButton(IdForWrite) {
  const randomWord = await getRandomWord();
  document.getElementById(IdForWrite).value = randomWord;
}

function listener(e) {
  if (e.target.id === 'crossButton') onCrossButton('searchInput');
  if (e.target.id === 'keyboardButton') onKeyboardButton();
  if (e.target.id === 'searchButton') onSearchButton('searchInput');
  if (e.target.id === 'randomWordButton') onRandomWordButton('searchInput');
}

function enterListener(e) {
  e.preventDefault();
  onSearchButton('searchInput');
}

export { listener, enterListener };
