async function getRandomWord() {
  const URL = 'https://api.wordnik.com/v4/words.json/randomWord?hasDictionaryDef=true&maxCorpusCount=-1&minDictionaryCount=5&maxDictionaryCount=-1&minLength=5&maxLength=-1&api_key=a2a73e7b926c924fad7001ca3111acd55af2ffabf50eb4ae5';
  const word = await fetch(URL)
    .then((res) => res.json())
    .then((data) => data);
  const randomWord = word.word;
  return randomWord;
}

export default getRandomWord;
