class Block {
  constructor(html, place) {
    this.html = html;
    this.place = place;
  }

  get whereIRender() {
    return this.place;
  }

  set whereIRender(where) {
    this.whereIRender = where;
  }

  render() {
    document.getElementById(this.place).insertAdjacentHTML('beforeend', this.html);
  }

  rerender() {
    this.clear();
    this.render();
  }

  clear() {
    [...document.getElementById(this.place).children].forEach((elem) => elem.remove());
  }
}

export default Block;
