import Block from './Block';


function footerHtml(text1, href1, text2, href2) {
  return `
  <div class="footer">
    <a class="footer__link" href="${href1}">${text1}</a>
    <a class="footer__link" href="${href2}">${text2}</a>
  </div>
`;
}

class Header extends Block {
  constructor(whereIRender, text1, href1, text2, href2) {
    super(footerHtml(text1, href1, text2, href2), whereIRender);
  }
}

export default Header;
