
function getImdbRate(id) {
  const RATE_URL = `https://www.omdbapi.com/?i=${id}&apikey=a8fdc458`;
  return fetch(RATE_URL);
}

async function getMoviesData(phrase) {
  const URL = `http://www.omdbapi.com/?s=${phrase}&apikey=a8fdc458`;
  const data = await fetch(URL);
  const response = await data.json();
  const moviesData = [];
  /* eslint-disable-next-line no-restricted-syntax */
  for (const movieData of response.Search) {
    /* eslint-disable-next-line no-await-in-loop */
    const data2 = await getImdbRate(movieData.imdbID);
    /* eslint-disable-next-line no-await-in-loop */
    const response2 = await data2.json();
    moviesData.push(response2);
  }

  return moviesData;
}

export default getMoviesData;
