function searchFieldHtml(hint, inputValue = '') {
  return `
  <div class="search-field">
    <div class="flex-row-box">
      <form class="flex-row-block_border" id="searchForm">
        <i id="randomWordButton" class="fas fa-random search-field__icon"></i>
        <input type="text" value="${inputValue}" placeholder="Search..." id="searchInput" class="search-field__input" autofocus autocomplete="off">
        <i id="crossButton" class="fas fa-times search-field__icon"></i>
        <i id="keyboardButton" class="fas fa-keyboard search-field__icon"></i>
      </form>
      <button id="searchButton" class="search-field__button">SEARCH</button>
    </div>
    <div class="search-field__hints">${hint}</div>
  </div>
`;
}

class SearchField extends Block {
  constructor(whereIRender, hint) {
    super(searchFieldHtml(hint), whereIRender);
    this.hint = hint;
    this.inputValue = 'hello';
  }

  setHintAndRerender(hint, inputValue) {
    this.hint = hint;
    this.inputValue = inputValue;
    this.html = searchFieldHtml(hint, inputValue);
    this.rerender();
  }
}

export default SearchField;
