async function getRuTranslate(ruPhrase) {
  const key = 'trnsl.1.1.20200506T105306Z.f3e6d014e9f309ea.2e01b69a37e80f8570a10b643acf85a075d1f761';
  const URL = `https://translate.yandex.net/api/v1.5/tr.json/translate?key=${key}&text=${ruPhrase}&lang=ru-en`;
  const translate = await fetch(URL)
    .then((res) => res.json())
    .then((data) => data);

  return translate.text;
}

export default getRuTranslate;
